@font-face {
    font-family: PangramSans;
    src: url("/assets/fonts/Pangram-Regular.otf") format("opentype");
}

@font-face {
    font-family: PangramSans;
    font-weight: bold;
    src: url("/assets/fonts/Pangram-Black.otf") format("opentype");
}

$gray-1: #f9f9f9;
$gray-2: #ccc;
$gray-3: #767676;
$gray-4: #515151;
$gray-5: #313131;

$red: #ac4142;
$orange: #d28445;
$yellow: #f4bf75;
$green: #90a959;
$cyan: #75b5aa;
$blue: #268bd2;
// $blue: #6a9fb5;
$brown: #8f5536;

$root-font-family: PangramSans, sans-serif;
$root-font-size: 16px;
$root-line-height: 1.5;

$body-color: #515151;
$body-bg: #fff;
$link-color: $blue;

$border-color: #e5e5e5;

$large-breakpoint: 38em;
$large-font-size: 20px;

$code-font-family: PangramSans, sans-serif;
